import React from "react";
import styled from "styled-components";

const SidebarContainer = styled.aside`
  width: 400px;
  height: calc(100vh - 70px); /* Header     */
  background-color: #081017;
  position: sticky;
  top: 70px; /* Header    */
  overflow: hidden;
  display: flex;
  flex-direction: column;
`;

const ScrollContainer = styled.div`
  padding: 0 10px 20px 10px; /*   ,     */
  overflow-y: auto;
  flex: 1;
  max-height: calc(100vh - 70px); /*     */

  /*   */
  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(215, 223, 228, 0.05);
    border-radius: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(215, 223, 228, 0.15);
    border-radius: 4px;

    &:hover {
      background: rgba(215, 223, 228, 0.25);
    }
  }
`;

const MenuSection = styled.div`
  margin-bottom: 24px;
  background: rgba(215, 223, 228, 0.08);
  border-radius: 12px;
  padding: 12px 8px;
`;

const MenuTitle = styled.h2`
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 700;
  font-size: 16px;
  color: #d7dfe4;
  padding: 12px 16px;
  margin: 0;
  border-bottom: 1px solid rgba(215, 223, 228, 0.1);
  margin-bottom: 8px;
`;

const SubMenu = styled.div`
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 4px;
`;

const MenuItem = styled.button`
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 500;
  font-size: 14px;
  color: #d7dfe4;
  padding: 10px 16px;
  background: ${(props) =>
    props.$isActive ? "rgba(35, 149, 233, 0.7)" : "transparent"};
  border: none;
  border-radius: 8px;
  text-align: left;
  cursor: pointer;
  width: 100%;
  transition: all 0.2s ease;

  &:hover {
    background: ${(props) =>
      props.$isActive ? "rgba(35, 149, 233, 0.7)" : "rgba(35, 149, 233, 0.4)"};
  }
`;

const menuData = [
  {
    title: "  ",
    items: ["  ", "", "", "", ""],
  },
  {
    title: "  SQL",
    items: [
      "",
      "  ",
      "   ",
      "NULL  ",
      " VS ",
    ],
  },
  {
    title: "SQL ",
    items: [
      "  ",
      "SELECT ",
      "",
      "WHERE ",
      "GROUP BY, HAVING ",
      "ORDER BY ",
      "",
      " ",
    ],
  },
  {
    title: "SQL ",
    items: [
      " ",
      " ",
      " ",
      " ",
      "TOP N ",
      "   ",
      "PIVOT UNPIVOT",
      " ",
    ],
  },
  {
    title: " ",
    items: ["DML", "TCL", "DDL", "DCL"],
  },
];

const Sidebar = ({ activeItem, onItemClick }) => {
  return (
    <SidebarContainer>
      <ScrollContainer>
        {menuData.map((section, sectionIndex) => (
          <MenuSection key={sectionIndex}>
            <MenuTitle>{section.title}</MenuTitle>
            <SubMenu>
              {section.items.map((item, itemIndex) => (
                <MenuItem
                  key={itemIndex}
                  $isActive={activeItem === item}
                  onClick={() => onItemClick(item)}
                >
                  {item}
                </MenuItem>
              ))}
            </SubMenu>
          </MenuSection>
        ))}
      </ScrollContainer>
    </SidebarContainer>
  );
};

export default Sidebar;
